body {
  text-align: center;
}
.slider {
  height: 500px;
  background-image: url(../public/img/slider.jpg);
  background-size: cover;
  background-position: center;
}
.start1 {
  opacity: 0;
}
.end1 {
  opacity: 1;
  transition: opacity 1s;
}
.start2 {
  transform: scale(0);
}
.end2 {
  transform: scale(1);
  transition: all 0.5s;
}
.btn {
  --bs-btn-padding-x: 0.6rem;
  --bs-btn-padding-y: 0.2rem;
}
